














import uniLoadMore from '@/components/uni-load-more.vue'
export default {
	components: {
		uniLoadMore
	},
	data() {
		return {
			list: [{
				images:"../../static/images/videor2_c2.jpg",
				title:"辣妈母婴",
				date:"21点20分"
			},{
				images:"../../static/images/videor2_c2.jpg",
				title:"辣妈母婴",
				date:"21点20分"
			},{
				images:"../../static/images/videor2_c2.jpg",
				title:"辣妈母婴",
				date:"21点20分"
			},{
				images:"../../static/images/videor2_c2.jpg",
				title:"辣妈母婴",
				date:"21点20分"
			}],
			loadingType: 0,
			contentText: {
				contentdown: "上拉显示更多",
				contentrefresh: "正在加载...",
				contentnomore: "没有更多数据了"
			}
		}
	},
	onReachBottom() {
		if (this.loadingType !== 0) {
			return;
		}
		this.loadingType = 1;
		let list = [],
			maxItem = this.list[this.list.length - 1],
			length = maxItem + 6;
		for (let i = maxItem + 1; i < length; i++) {
			list.push(i);
		}
		setTimeout(() => {
			if (length > 26) {
				this.loadingType = 2;
				return;
			}
			this.list = this.list.concat(list);
			this.loadingType = 0;
		}, 800);
	}
}

